@import "../variable/variable";
#about {
  padding: 48px 0 80px;
  h2 {
    font-family: "Montserrat", sans-serif;
    font-size: 48px;
    font-weight: 700;
    color: $primary-color;
    margin-top: 48px;
    margin-bottom: 32px;
    text-align: center;
  }
  .text {
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    font-size: 18px;
    color: $secondary-color;
    width: 1056px;
    margin: 0 auto 80px;
    text-align: center;
  }
  img {
    width: 100%;
  }
  .wrapper {
    padding: 32px;
    border: 1px solid $secondary-color;
    border-radius: 16px;
    .scroll {
      padding-bottom: 10px;
      border-bottom: 1px solid $secondary-color;
      h3 {
        margin-bottom: 32px;
        font-family: "Poppins", sans-serif;
        font-size: 18px;
        font-weight: 700;
      }
      .scrolling-container {
        width: 100%; /* Set your desired width */
        height: 350px; /* Set your desired height */
        overflow: auto; /* Enable scrolling when content overflows */
        border: none; /* Optional border for visual clarity */
        padding-left: 0;
        li {
          margin-bottom: 16px;
          a {
            font-family: "Poppins", sans-serif;
            font-size: 18px;
            font-weight: 400;
            color: $secondary-color;
          }
        }
      }
      .scrolling-container::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
        background-color: #f5f5f5;
      }
      .scrolling-container::-webkit-scrollbar {
        width: 6px;
        background-color: $brand-color;
      }
      .scrolling-container::-webkit-scrollbar-thumb {
        background-color: $brand-color;
      }
    }
    .filter {
      .according_filter {
        button {
          border: none;
          font-family: "Poppins", sans-serif;
          font-size: 18px;
          font-weight: 500;
          color: $primary-color;
          padding-left: 0;
          padding-right: 0;
        }
        .accordion-item {
          border: none;
        }
        .accordion-button:focus {
          z-index: 3;
          border: none;
          outline: 0;
          box-shadow: none;
        }
        .accordion-button:not(.collapsed) {
          color: $primary-color;
          background-color: transparent;
          box-shadow: none;
        }
        .slidecontainer {
          width: 100%;
        }
        .slider {
          width: 100%;
          background: red;
          accent-color: $brand-color;
        }
        .slider::-webkit-slider-thumb {
          background: red; /* Set the default color for the thumb */
        }
      }
    }
    .location_filter {
      .local_filter {
        button {
          border: none;
          font-family: "Poppins", sans-serif;
          font-size: 18px;
          font-weight: 500;
          color: $primary-color;
          padding-left: 0;
          padding-right: 0;
        }
        .accordion-item {
          border: none;
        }
        .accordion-button:focus {
          z-index: 3;
          border: none;
          outline: 0;
          box-shadow: none;
        }
        .accordion-button:not(.collapsed) {
          color: $primary-color;
          background-color: transparent;
          box-shadow: none;
        }
        .main .custombox:after {
          left: 6px;
          bottom: 4px;
          width: 10px;
          height: 18px;
          border: solid white;
          border-width: 0 4px 4px 0;
          transform: rotate(45deg);
          -ms-transform: rotate(45deg);
          transform: rotate(45deg);
        }
        .main {
          display: block;
          position: relative;
          padding-left: 45px;
          margin-bottom: 15px;
          cursor: pointer;
          font-size: 20px;
          .color {
            font-family: "Poppins", sans-serif;
            font-weight: 400;
            font-size: 18px;
            color: $secondary-color;
          }
          &:hover .check ~ .custombox {
            background-color: #008000;
          }
          .check {
            visibility: hidden;
            &:checked ~ .custombox {
              background-color: $brand-color;
              border: none;
            }
            &:checked ~ .custombox ~ .color {
              color: $brand-color;
            }
            &:checked ~ .custombox:after {
              display: block;
            }
          }
          .custombox {
            position: absolute;
            top: 5px;
            left: 0;
            width: 24px;
            height: 24px;
            background: transparent;
            border: 1px solid $secondary-color;
            &::after {
              content: "";
              position: absolute;
              display: none;
            }
          }
        }
      }
      // .slidecontainer {
      //   width: 100%;
      // }
