    .btns {
      padding: 16px 100px;
      background: $brand-color;
      color: #fff;
      font-family: "Poppins", sans-serif;
      font-weight: 500;
      font-size: 18px;
      border: none;
      margin-top: 56px;
    }
    .reset {
      font-family: "Poppins", sans-serif;
      font-weight: 500;
      font-size: 18px;
      color: $brand-color;
      margin: 32px auto 0;
      display: block;
      width: fit-content;
    }
  }
  .information {
    display: flex;
    justify-content: start;
    align-items: center;
    gap: 15px;
    margin-bottom: 15px;
    &:last-child {
      margin-bottom: 0;
    }
    .item {
      padding: 20px 15px;
      border-radius: 16px;
      font-family: "Poppins", sans-serif;
      font-size: 18px;
      width: 230px;
      font-weight: 400;
      color: $primary-color;
      border: 1px solid $secondary-color;
      transition: all 0.4s;
      &:hover {
        color: $brand-color;
        cursor: pointer;
      }
      span {
        display: block;
        width: fit-content;
        margin: 0 auto;
        font-size: 24px;
      }
      p {
        width: max-content;
        margin: 0 auto;
      }
    }
  }
  .feture {
    margin-top: 80px;
    h3 {
      font-family: "Montserrat", sans-serif;
      font-weight: 700;
      font-size: 24px;
      color: $primary-color;
      margin-bottom: 32px;
    }
    .other_side {
      display: flex;
      justify-content: space-between;
      align-items: center;
      a {
        color: $secondary-color;
        font-size: 40px;
        font-weight: 400;
      }
    }
  }
  .btm {
    margin-bottom: 32px;
  }
  .list_product {
    .ratedDiv {
      width: 100%;
      padding: 32px 32px;
      border-radius: 32px;
      border: 1px solid $secondary-color;
      img {
        width: 100%;
        border-radius: 32px;
      }
      h3 {
        font-family: "Poppins", sans-serif;
        font-size: 24px;
        font-weight: 400;
        color: #383838;
        margin-top: 32px;
        text-align: center;
      }
      p {
        font-family: "Poppins", sans-serif;
        font-size: 24px;
        font-weight: 700;
        color: #383838;
        display: block;
        margin: 0 auto 16px;
        text-align: center;
      }
      h4 {
        display: flex;
        justify-content: space-around;
        font-family: "Poppins", sans-serif;
        font-size: 18px;
        font-weight: 400;
        color: #383838;
        width: 158px;
        margin: 0 auto;
        i {
          color: #ffd687;
        }
        .line {
          height: auto;
          width: 1px;
          background: #383838;
        }
      }
      .love {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 24px;
        a {
          padding: 12px 28px;
          background: $brand-color;
          color: #fff;
          border-radius: 8px;
          font-family: "Poppins", sans-serif;
          font-size: 18px;
          font-weight: 400;
          width: fit-content;
          margin: 0 auto;
        }
        i {
          font-size: 18px;
